import React, {Component} from "react";
import './form.css';

export default class Form extends Component {
    constructor(props) {
        super(props);
        this.handleSubmit=this.handleSubmit.bind(this);
    }

    handleSubmit(event){
        event.preventDefault();
        var myForm = new FormData(event.target);
        fetch("http://127.0.0.1:5000/save_booking", {
            method:"POST",
            body:myForm,
        })
        .then(response=> response.json())
        .then(data=>console.log("server response", data));
    }

    render() {
        return <div className='div-form'>
            <form onSubmit={this.handleSubmit} className='form'>
                <label>Full Name</label>
                <input type='text' name='fullname'></input>
                <label>Date from:</label>
                <input type='date' name='datefrom'></input>
                <label>Date to:</label>
                <input type='date' name='dateto'></input>
                <label>Phone number:</label>
                <input type='text' name='tel'></input>
                <button type='submit'>Send booking</button>
            </form>
        </div>
    }


}

