import './content.css'

export default function Content(props) {
    return <div className='container-div'>
        <div className='container-item'>
            <img src={props.icon1}></img>
            <h2>{props.title1}</h2>
            <p>{props.text}</p>
        </div>

        <div className='container-item'>
            <img src={props.icon2}></img>
            <h2>{props.title2}</h2>
            <p>{props.text}</p>
        </div>

        <div className='container-item'>
            <img src={props.icon3}></img>
            <h2>{props.title3}</h2>
            <p>{props.text}</p>
        </div>
    </div>
}