import React, {Component} from "react";
import './data.css';

export default class Data extends Component {
    constructor(props){
        super(props);
        this.state = {
            loading:true,
            info:[]
        };
    }
    

    async componentDidMount() {        
        const response = await fetch("http://127.0.0.1:5000/bookings");
        const data = await response.json();
        this.setState({
            loading:false,
            info:data.info
        })
        console.log(this.state.info);
}


    render() {
        return <div>
            {this.setState.loading ? <div>Loading...</div> 
            : <div className='data-div'>
            <h1>Booking Items List</h1>
                <table>
                <tr>
                        <th>Nume</th>
                        <th>Data</th>
                        <th>Ora</th>
                        <th>Telefon</th>
                    </tr>
                    {this.state.info.map(subscription => {
                            let arr=subscription.split(',');
                            return <tr>
                                <td>{arr[0]}</td>
                                <td>{arr[1]}</td>
                                <td>{arr[2]}</td>
                                <td>{arr[3]}</td>
                            </tr>
                        })
                    }
                    </table> 
            </div>}
        </div>

    }
}